import React, { useEffect } from 'react';
import WishWall from './components/WishWall';

function App() {
  useEffect(() => {
    // Cp nht title v meta description
    document.title = "Bc Tng iu c";
    const metaDescription = document.querySelector('meta[name="description"]');
    if (metaDescription) {
      metaDescription.setAttribute("content", "Ni gi gm nhng iu c v li chc ca bn");
    }
  }, []);

  return (
    <div className="min-h-screen py-8 bg-gradient-to-b from-gray-50 to-gray-100">
      <WishWall />
    </div>
  );
}

export default App; 